import React from "react"
import Header from "../components/Header"
import styled from "styled-components"
import AniLink from "gatsby-plugin-transition-link/AniLink"

import "../components/layout.css"

const AboutSection = styled.section`
  text-align: center;
`
const AboutTitle = styled.h1`
  font-size: 70px;
  margin-bottom: 10px;
`

const AboutSubtitle = styled.p`
  font-size: 22px;
  max-width: 850px;
  margin: 22px auto;
  letter-spacing: 1px;
`
const AboutButton = styled.button`
  font-family: "Nunito", sans-serif;
  width: 300px;
  cursor: pointer;
  color: #fff;
  border: none;
  outline: none;
  height: 65px;
  margin-top: 22px;
  background-color: #252d4f;
  font-size: 16px;
  -webkit-box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
  box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
`

const about = () => {
  return (
    <div className="wrapper">
      <Header />
      <AboutSection>
        <AboutTitle>About Me</AboutTitle>
        <AboutSubtitle>
          Hello, my name is Michael Diggs. I'm a web developer who spends most
          of my days coding, designing, or learning more about the web. For over
          a year now, I've been cultivating and improving my skills to produce
          beautiful and professional work.
        </AboutSubtitle>
        <AniLink paintDrip duration={0.4} hex="#212840" to="/projects/">
          <AboutButton>View My Projects</AboutButton>
        </AniLink>
      </AboutSection>
    </div>
  )
}

export default about
